import { defineStore } from 'pinia';
import { Store } from "@/types";
import { ref } from 'vue';
import { ApiService } from '@/API/apiServices';
import { useRoute } from 'vue-router';
import { stores } from '@/fixtures';
import { accounts } from "@/fixtures";

const fillerStoreName: Store = {
    id: 0,
    storeName: "your store name",
    storeID: 10000001,
    storeAddress: {
      locationID: 11111111,
      locationCity: "New York City",
      locationState: "New York",
      locationCountry: "United States",
      locationStreet: "Your store address 87",
      locationZipcode: "123 12"
    },
    customerServices: [
      "Buy product",
      "Return product",
      "Ask staff for help",
      "Other requests"
    ],
    storeQueues: [
        {
            queueNumber: 101,
            queueCategory: "Buy product"
        }
    ]
  };

export const useShop = defineStore('shop', () => {
    const shop = ref<Store>();
    const isDataLoaded = ref(false);

    const fetchShop = async (shopId: string) => {
        // eslint-disable-next-line no-useless-catch
        try {
            // tEMPORARY SOLUTION
            const route = useRoute();
            if(route.fullPath.includes("proj_queue_test")) {
                // eslint-disable-next-line no-prototype-builtins
                if(stores.hasOwnProperty(Number(shopId))) shop.value = stores[Number(shopId)];
                else shop.value = fillerStoreName;
                isDataLoaded.value = true;
            }
            else {
                const { data } = await ApiService.fetchStore(shopId);
                if(Object.keys(data).length === 0) 
                    shop.value = fillerStoreName;
                else {  
                    shop.value = data as Store;
                }
                isDataLoaded.value = true;
            }
        }
        catch(error){
            shop.value = fillerStoreName;
            isDataLoaded.value = true;
        }
    }

    const addShop = async (store: Store) => {
        // eslint-disable-next-line no-useless-catch
        try {
            // tEMPORARY SOLUTION
            const route = useRoute();
            if(route.fullPath.includes("proj_queue_test")) stores.push(store)
            else await ApiService.postStore(store);
        }
        catch(error){
            console.log(" ~ file: shop.ts:68 ~ addShop ~ error:", error)
            
            shop.value = fillerStoreName;
            isDataLoaded.value = true;
        }
    }

    return {
        fetchShop,
        isDataLoaded,
        shop,
    }
});