import { accounts } from "./accounts";
import { stores } from "./stores";
import { Router } from "vue-router";

export async function fakeLogin(username: string, password: string, router: Router): Promise<string> {
  await new Promise((resolve) => setTimeout(resolve, 1000));

  const matchedAccountIdx = accounts.findIndex(
    (account) =>
      account.accountUsername === username &&
      account.accountPassword === password
  );

  if (matchedAccountIdx !== -1) {
    const shopID = accounts[matchedAccountIdx].accountShop;
    const queryShop = { shopID };
    const accID = accounts[matchedAccountIdx].accountID;
    const queryAcc = { accID };    
    const matchedShopsIdx = stores.findIndex((store) => store.storeID === shopID);



    if (accounts[matchedAccountIdx].accountPermission === "Admin") {
      await router.push({ path: "/admin", query: queryAcc });
      return accounts[matchedAccountIdx].accountUsername;
    } 
    else if (accounts[matchedAccountIdx].accountPermission === "Cashere") {
      await router.push({ path: "/shop", query: queryAcc });
      return accounts[matchedAccountIdx].accountUsername;
    }
    else if (accounts[matchedAccountIdx].accountPermission === "Shop owner") {
      await router.push({ path: "/owner", query: queryAcc });
      return accounts[matchedAccountIdx].accountUsername;
    }

    return 'fake-token';
  } else {
    throw new Error('Invalid credentials');
  }
}
